/**
 * Scroll container to activeElement
 */
export function scrollToActive(activeItem: HTMLElement): void {
  const activeItemContainer: HTMLElement = activeItem ? activeItem.parentElement : null;

  if (activeItem && activeItemContainer && (activeItemContainer.scrollHeight > activeItemContainer.clientHeight)) {
    const scrollBottom = activeItemContainer.clientHeight + activeItemContainer.scrollTop;
    const elementBottom = activeItem.offsetTop + activeItem.offsetHeight;

    if (elementBottom > scrollBottom) {
      activeItemContainer.scrollTop = elementBottom - activeItemContainer.clientHeight;
    } else if (activeItem.offsetTop < activeItemContainer.scrollTop) {
      activeItemContainer.scrollTop = activeItem.offsetTop;
    }
  }
}
